//////////////////////
// default
//////////////////////

// .transition--default();

/* USAGE:
.transition--default.out();
opacity: 0;
&:hover, &.hover, &:active, &.active {
  .transition--default.active();
   opacity: 1;
}
*/

/* USAGE:
&.active {
  .inner {
    .transition--default.active();
    opacity: 1;
  }
}
&.out {
  .inner {
    .transition--default.out();
    opacity: 0;
  }
}
*/

.transition--default() {
  &.active {
    transition-property: all;
    transition-duration: @time--medium;
    transition-timing-function: @bez--in;
  }
  &.out {
    transition-property: all;
    transition-duration: @time--medium;
    transition-timing-function: @bez--out;
  }
}

// .animation--default();

/* USAGE:
.anim--opacity.active();
&:hover, &.hover, &:active, &.active {
  .anim--opacity.out();
}
*/

/* USAGE:
&.active {
  .inner {
    .anim--opacity.active();
  }
}
&.out {
  .inner {
    .anim--opacity.out();
  }
}
*/

.animation--default() {
  &.active {
    animation-duration: @time--medium;
    animation-timing-function: @bez--in;
  }
  &.out {
    animation-duration: @time--medium;
    animation-timing-function: @bez--out;
  }
}

//////////////////////
// animations
//////////////////////

// anim--opacity

.anim--opacity {
  &.active {
    .animation--default.active();
    animation-name: anim--opacity-in;
    animation-fill-mode: forwards;
    opacity: 0;
  }
  &.out {
    .animation--default.out();
    animation-name: anim--opacity-out;
    animation-fill-mode: forwards;
    opacity: 1;
  }
}

@keyframes anim--opacity-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes anim--opacity-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

// anim--zoom

.anim--zoom {
  &.active {
    .animation--default.active();
    animation-name: anim--zoom-in;
    animation-fill-mode: forwards;
    opacity: 0;
  }
  &.out {
    .animation--default.out();
    animation-name: anim--zoom-out;
    animation-fill-mode: forwards;
    opacity: 1;
  }
}

@keyframes anim--zoom-in {
  0% {
    transform: scale(.9, .5);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes anim--zoom-out {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(.9, .5);
    opacity: 0;
  }
}

