//////////////////////
// custom
//////////////////////

// collapse

.collapse--height,
.collapse--width {
  &.active {
    .transition--default.active();
    transition-delay: @time--small;
  }
  &.out {
    .transition--default.out();
    transition-delay: @time--medium;
  }
}
